import React, { useContext } from "react";
import { Box, ResponsiveContext, Stack, Text } from "grommet";
import { Cart } from "grommet-icons";

export const SCart = ({ num, size }) => {
  const csize = useContext(ResponsiveContext);
  return (
    <Stack anchor="top-right">
      <Cart size={size} />
      <Box
        background="orange"
        pad={{ horizontal: csize === "small" ? "4px" : "6px" }}
        round
      >
        <Text size="small">{num}</Text>
      </Box>
    </Stack>
  );
};
