import { Header, ResponsiveContext } from "grommet";
import SnackBar from "./SnackBar";

import Navbar from "./Navbar";

const NavHeader = ({ num }) => {
  return (
    <Header
      direction="row"
      justify="between"
      elevation="small"
      fill="horizontal"
      as="header"
    >
      <ResponsiveContext.Consumer>
        {(size) =>
          size === "small" ? <SnackBar num={num} /> : <Navbar num={num} />
        }
      </ResponsiveContext.Consumer>
    </Header>
  );
};

export default NavHeader;
